body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  position: relative;
  color: white;
  background-image: linear-gradient(45deg, #F78361,
  #F54B64);

}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.hero{
  color: white;
  height: 120vh;
  background-image: url("./components/assets/background2.png");
  background-size: cover;
  background-position: center;
}
.hero1{
  position: relative;

}
.heroContext{
  position: absolute;
  top: 175px;
  left: 100px;
}
.heroHead {
  font-family: 'Roboto', sans-serif;
  color: white ;
}

.heroSubTxt{
  width: 75%;
  color: #F54B64 ;
  font-size: 1.2rem;
  font-family: 'Libre Baskerville', serif;
  font-style: italic;}
.navBar{
  /* border: 1px solid plum; */
  /* border: 1px solid red; */
  width: 60%;
  margin: 0 auto;
  position: absolute;
  left: 0;
  right: 0;
}

ul li {
  list-style: none;
  /* margin: 0 auto; */
}
ul  {
  margin: 0 auto;
  padding: 0;
}
.nav-item {
  /* padding: 15px; */
}
.nav-item a{
  color: white;
  border-radius: 30px;
  padding: 15px 45px;
  transition: ease all .5s;
}
.nav-item a:hover {
  background: #2a253e;
  color: white;
}
.myBtn{
  width: 200px;
  cursor: pointer;
  /* margin: 0 auto; */
  background-image: linear-gradient(45deg, #F54B64,
  #F78361);
  color: white;
  padding: 15px 30px;
  border-radius: 30px;
  filter: drop-shadow( 3px 3px 6px #000000);
  transition: ease all .5s;

  
}
.myBtn:hover{
  background-image: linear-gradient(45deg, #F78361,
  #F54B64);
}
.myBtn:active{
  filter: drop-shadow( 0px 0px 0px #1a172500);
  /* border: none; */
}
.aboutMe {
  height: 75vh;
  background: #221b30;
  margin: 80px auto;
  padding: 50px 100px;
  border-radius: 10px;
  filter: drop-shadow( 0px 0px 8px #1a1725);
}
img {
  width: 100%;
}
.rndImg{
  margin-left: 50px;
  width: 50vh;
  height: 50vh;
  object-fit: cover;
  border-radius: 50%;
}
.pinkTxt {
  color: #F54B64;
}